import React, { forwardRef, useContext, useRef } from 'react';
import {
    AriaRadioProps,
    useFocusRing,
    useId,
    useRadio,
    VisuallyHidden,
} from 'react-aria';
import type { RadioGroupState } from 'react-stately';
import { mergeProps, useObjectRef } from '@react-aria/utils';
import classNames from 'classnames';

import { RadioContext } from '../RadioGroup/RadioGroup';
import { StackModule } from '../Stack';
import type { DescriptionSeverity } from '../types';
import { Text } from '../Typography';

import styles from './Radio.module.scss';

export interface RadioProps extends AriaRadioProps {
    description?: string;
    descriptionSeverity?: DescriptionSeverity;
    suffix?: string;
}

const Radio = forwardRef<HTMLInputElement, RadioProps>((props, ref) => {
    const forwardedRef = useObjectRef(ref);
    const fallbackRef = useRef(null);
    const currentRef = forwardedRef || fallbackRef;
    const {
        children,
        description,
        descriptionSeverity = 'regular',
        suffix,
    } = props;

    const state = useContext(RadioContext);

    const { inputProps, isSelected, isPressed, labelProps } = useRadio(
        props,
        state as RadioGroupState,
        currentRef
    );
    const { isFocusVisible, focusProps } = useFocusRing();

    const descriptionId = useId();

    const radioClassList = classNames(styles.radio, {
        [styles[`radio--pressed`]]: isPressed,
        [styles[`radio--selected`]]: isSelected,
        [styles[`radio--focused`]]: isFocusVisible,
        [styles[`radio--invalid`]]: state?.isInvalid,
    });

    const descriptionClassList = classNames({
        [styles['messages__description--regular']]:
            descriptionSeverity === 'regular',
        [styles['messages__description--warning']]:
            descriptionSeverity === 'warning',
    });

    // Remove aria-describedby from react-aria props and concat with internal id's to keep it usable
    const { 'aria-describedby': ariaDescribedBy, ...restInputProps } =
        inputProps;

    return (
        <StackModule>
            <label className={radioClassList} {...labelProps}>
                <VisuallyHidden>
                    <input
                        {...mergeProps(restInputProps, focusProps)}
                        ref={currentRef}
                        aria-describedby={`${descriptionId} ${ariaDescribedBy}`}
                    />
                </VisuallyHidden>

                <span className={styles.radio__indicator} aria-hidden="true">
                    {/* TODO: replace with icon from library when it is added */}
                    <svg
                        width="16"
                        height="16"
                        viewBox="0 0 16 16"
                        xmlns="http://www.w3.org/2000/svg"
                        className={styles.radio__icon}
                    >
                        <circle cx="8" cy="8" r="5" />
                    </svg>
                </span>

                <Text.Label size="default-400" className={styles.radio__label}>
                    {children}
                    {suffix && (
                        <>
                            {/* Use an extra element as separator so everything stays inline and no extra margin on either side needed, which doesn't look right when text starts wrapping */}
                            <span
                                className={styles['radio__suffix-separator']}
                            ></span>
                            <span className={styles.radio__suffix}>
                                {suffix}
                            </span>
                        </>
                    )}
                </Text.Label>
            </label>

            {description && (
                <div className={styles.messages}>
                    <Text.Body
                        variant="line"
                        size="small-400"
                        id={descriptionId}
                        className={descriptionClassList}
                    >
                        {description}
                    </Text.Body>
                </div>
            )}
        </StackModule>
    );
});

Radio.displayName = 'Radio';

export default Radio;
